import React from 'react';
import Img from '../img/img.component';
import icone1 from '../../images/Emoticon/digiperes.svg';
import ItemMenu from '../item-menu/itemmenu.component';
import Button from '../button/button.component';
const Menu = () => {
    return (
        <div className='flex flex-col space-y-6 container mx-auto md:flex-row justify-between'>
            <div className='flex gap-8 items-center'>
                <Img src={icone1} />
                <ItemMenu/>
            </div>
            <div className='m-auto md:m-0'>
                <Button className='bg-buttonmenu pt-3 pb-3 pr-4 pl-4 mb-2 ' title='دانلود اپلیکیشن'/>
            </div>
        </div>
    );
};

export default Menu;