import React from 'react';
import { Link } from 'react-router-dom';
import icone1 from '../../images/Emoticon/big-img2.png';
import icone2 from '../../images/Emoticon/icons8-chevron-down-24.png';
import Button from '../button/button.component';
import Img from '../img/img.component';
import Paragraph from '../paragraph/paragraph';
import Title from '../title/title.component';

const Row1 = () => {
    return (
        <div className='container mx-auto pr-4 pl-4 relative'>
            <div className='flex space-y-10 flex-col gap-8 items-center md:flex-row justify-between'>
                <div className='space-y-10 w-full'>
                    <Title fonsize='text-5xl mt-10 md:text-6xl md:mt-0' color='text-title' title='دیجی اکسپرس' />
                    <Paragraph color='text-title' fonsize='text-base' title='با دیجی‌اکسپرس هروقت بخوای میتونی مرسوله رو به مقصد برسونی و ازین طریق درآمد کسب کنی!' />
                  <div> <Link to='/signup'> <Button className='bg-white w-60 h-10' title='مشاهده پیش نیازها و ثبت ‌نام' /></Link></div>
                </div>
                <div style={{marginTop:0,marginBottom:60}}>
                    <Img src={icone1} />
                </div>
            </div>
            <div className='absolute -bottom-5 m-auto left-0 right-0'>
                <Paragraph textalign='text-center' color='text-white' className='mb-2' fonsize='text-base' title='اطلاعات بیشتر' />
                <button className='bg-white shadow-2xl m-auto rounded-full h-10 w-10 flex items-center justify-center'><Img classNmae='h-4' src={icone2} /></button>
            </div>
        </div>
    );
};

export default Row1;