import React from 'react';
import Footer from '../component/footer/footer.component';
import Menu from '../component/menu/menu.component';
import Row1SignUp from '../component/row-signup/row1sighup1';
import Row2 from '../component/row2-signup/row2signup';

const SignUp = () => {
    return (
        <div>
            <div className='sticky top-0 z-50 bg-white'>
                <Menu />
            </div>
            <div className='row1-signup'>
                <Row1SignUp />
            </div>
            <div className='bg-secondbackground'>
                <Row2/>
            </div>
            <div className='bg-footer'>
                <Footer />
            </div>
        </div>
    );
};

export default SignUp;