import React, { useState } from 'react';
import Img from '../img/img.component';
import Paragraph from '../paragraph/paragraph';
import Title from '../title/title.component';
import icone1 from '../../images/Emoticon/phone.png';
import icone4 from '../../images/Emoticon/image1.jpg';

type model = {
    src?: string;
    title?: string;
    paragraph?: string;
    mobile:string;
}
type BoxsType = {
    model: model[];
}
const Boxs = (props: BoxsType) => {
    const [number, setnumber] = useState<number>(0);
    const [image, setimage] = useState<string>(icone4);
    const onClick = (index: number,icone:string) => {
        setnumber(index);
        setimage(icone)
    }
    return (
        <div>
            {props.model.map((item, index) => {
                const className = ('flex p-8' + ' ' )+ (index === number ? 'bg-secondbackground border border-black rounded-lg shadow-2xl':'')
                return (
                    <div onClick={() => onClick(index,item.mobile)} className={className}>
                        <Img classNmae='h-8' src={item.src} />
                        <div className='mr-4 space-y-3'>
                            <Title fonsize='text-2xl' color='text-white' title={item.title} />
                            {number === index && <Paragraph fonsize='text-base' color='text-white' title={item.paragraph} />}
                        </div>
                    </div>
                )
            })}
            <div className='absolute left-5 sm:left-40 bottom-0'>
            <Img classNmae='relative phone' src={icone1} />
            <div className='absolute -bottom-0 left-0 right-0'>
            <Img classNmae='image-phone m-auto' src={image} />
            </div>
            </div>
        </div>
    );
};

export default Boxs;