import React from 'react';
import Paragraph from '../paragraph/paragraph';
import ProfileTitle from '../profile-title/profile-titel';
import Title from '../title/title.component';
import icone1 from '../../images/Emoticon/icons8-car-insurance-24.png';
import icone2 from '../../images/Emoticon/icons8-truck-24.png';
import icone3 from '../../images/Emoticon/icons8-mobile-phone-24.png';
import icone4 from '../../images/Emoticon/icons8-marker-24.png';
const Row1SignUp = () => {
    const data = [
        {src: icone1, title:'داشتن گواهینامه رانندگی' },
        {src: icone3, title:'داشتن گوشی هوشمند' },
        {src:icone2 , title:'داشتن خودرو معمولی یا موتور سیکلت' },
        {src: icone4, title:'سکونت در یکی از شهرهای:' , paragraph:'تهران، کرج، مشهد, تبریز, اهواز, شیراز, قم, اصفهان'},
    ]
    return (
        <div className='container mx-auto grid place-items-center pr-4 pl-4 pt-20 pb-20 grid-cols-1 gap-4 md:grid-cols-3'>
            <div>
            <Title color='text-black' fonsize='text-3xl mt-10 md:text-4xl md:mt-0' title='شرایط ثبت نام' />
            <Paragraph className='mt-4' color='text-title' fonsize='text-base' title='برای اینکه یکی از راننده‌های دیجی‌اکسپرس بشید کافیه شرایط روبرو رو داشته باشید و اطلاعاتتون رو وارد کنید' />
            </div>
            <div className='md:col-span-2 grid-cols-1 md:grid grid-cols-2 gap-4 '>
                <ProfileTitle model={data}/>
            </div>
        </div>
    );
};

export default Row1SignUp;