import React from 'react';
import Title from '../title/title.component';
import icone1 from '../../images/Emoticon/digiexpress-logo-yellow.19b700e5.svg';
import icone2 from '../../images/Emoticon/icons8-calendar-30.png';
import icone3 from '../../images/Emoticon/icons8-home-30.png';
import Img from '../img/img.component';
import Boxs from '../boxs/boxs.component';
import icone4 from '../../images/Emoticon/image1.jpg';
import icone5 from '../../images/Emoticon/image2.jpg';
import icone6 from '../../images/Emoticon/image3.jpg';

const Row3 = () => {
    const data = [
        { src: icone3, title: 'برنامه ریزی دلخواه', mobile: icone4, paragraph: 'برای روزهای هفته می‌تونی از قبل برنامه‌ریزی کنی!' },
        { src: icone2, title: 'زمان و مکان دلخواه', mobile: icone5, paragraph: 'تو محل و در زمان دلخواهت می‌تونی سفارش بگیری!' },
        { src: icone3, title: 'درآمد تضمین شده', mobile: icone6, paragraph: 'درآمدت تضمین شده‌است!' },
        { src: icone2, title: 'واریز آنی', mobile: icone4, paragraph: 'درآمدت بعد از هر سفر تو کیف پولته.' },
    ]
    return (
        <div className='container mt-20 mx-auto space-y-16 pr-4 pl-4'>
            <div className='flex gap-4 justify-center pt-16'>
                <Title color='text-white' fonsize='text-3xl mt-10 md:text-4xl md:mt-0' title='مزایای' />
                <Img src={icone1} />
            </div>
            <Boxs model={data} />
        </div>
    );
};

export default Row3;