import React from 'react';

type ImgType = {
    src?: string;
    textalign?: 'text-left' | 'text-center' | 'text-right' | undefined;
    classNmae?: string;
}
const Img = (props: ImgType) => {
    const className = props.textalign + ' ' + props.classNmae;
    return (
        <img className={className} src={props.src} alt="" />
    );
};

export default Img;