import React from 'react';

type ParagraphType = {
    title?: string;
    color?: string;
    fonsize?: string;
    textalign?: 'text-left' | 'text-center' | 'text-right' | undefined;
    className?: string;
}
const Paragraph = (props: ParagraphType) => {
    const className = (props.color ? props.color : 'text-itmemenu') + ' ' + (props.fonsize ? props.fonsize : 'text-base') + ' ' +( props.textalign + ' ' )+ (props.className);
    return (
        <p className={className}>{props.title}</p>
    );
};

export default Paragraph;