import React from 'react';
import Img from '../img/img.component';
import Paragraph from '../paragraph/paragraph';
import Title from '../title/title.component';

type model = {
    src?: string;
    bgImg?: string;
    title?: string;
    paragraph?: string;
}
type ImgBoxsType = {
    model: model[];
}
const ImgBoxs = (props: ImgBoxsType) => {
    return (
        <>
            {props.model.map(item => {
                const className = ('shadow-2xl m-auto rounded-full h-20 w-20 flex items-center justify-center') + ' ' + item.bgImg;
                return (
                    <div className='space-y-2'>
                        <div className={className}><Img classNmae='' src={item.src} /></div>
                        <Title textalign='text-center' fonsize='text-xl md:mt-0' color='text-title' title={item.title} />
                        <Paragraph textalign='text-center' fonsize='text-base' title={item.paragraph} />
                    </div>
                )
            })}
        </>
    );
};

export default ImgBoxs;