import React from 'react';
import Title from '../title/title.component';

type InputType = {
    title?: string;
}
const Input = (props: InputType) => {
    return (
        <div className='mt-3'>
            <Title className='mr-3' fonsize='text-xl' color='text-white' title={props.title} />
            <input className='w-full bg-white rounded-lg p-3 mt-2' type="text" />
        </div>
    );
};

export default Input;