import React from 'react';
import Img from '../img/img.component';
import Paragraph from '../paragraph/paragraph';
import Title from '../title/title.component';

type model = {
    src?: string;
    title?: string;
    paragraph?: string;

}
type ProfileTitleType = {
    model: model[];
}
const ProfileTitle = (props: ProfileTitleType) => {
    return (
        <>
            {props.model.map(item => {
                return (
                    <div className='flex items-center mb-4 gap-4'>
                        <div>
                        <div className='rounded-full bg-gray-100 h-14 w-14 flex items-center justify-center'>
                            <div className='rounded-full h-10 w-10 flex items-center justify-center bg-white'>
                            <Img classNmae='h-6' src={item.src} />
                            </div>
                        </div>
                        </div>
                        <div>
                            <Title color='text-black' fonsize='text-lg md:mt-0' title={item.title}/>
                            {item.paragraph && <Paragraph color='text-black' fonsize='text-sm' title={item.paragraph} />}
                        </div>
                    </div>
                )
            })}
        </>
    );
};

export default ProfileTitle;