import React from 'react';
import Img from '../img/img.component';

type ButtonType = {
    title?:string;
    className?:string;
    icone?:string;
}
const Button = (props:ButtonType) => {
    const className = 'rounded-lg text-colorbutton gap-1 text-sm flex items-center justify-center' +' '+ props.className;
    return (
       <button className={className}>
           {props.icone&&<Img src={props.icone}/>}
           {props.title}
           </button>
    );
};

export default Button;