import React from 'react';

type TitelType = {
    title?: string;
    color?: string;
    fonsize?: string;
    textalign?: 'text-left' | 'text-center' | 'text-right' | undefined;
    className?: string;
}
const Title = (props: TitelType) => {
    const className = (props.color ? props.color : 'text-itmemenu') + ' ' + (props.fonsize ? props.fonsize : 'text-base') + ' ' + (props.textalign + ' ') + (props.className);
    return (
        <h1 className={className}>{props.title}</h1>
    );
};

export default Title;