import React from 'react';
import Footer from '../component/footer/footer.component';
import Menu from '../component/menu/menu.component';
import Row1 from '../component/row1/row1.component';
import Row2 from '../component/row2/row2';
import Row3 from '../component/row3/row3.component';
import Row4 from '../component/row4/row4.component';
import icone1 from '../images/Emoticon/truck-full.7bb0c033.svg';

const Home = () => {
    return (
        <div>
            <div className='sticky top-0 z-50 bg-white'>
                <Menu />
            </div>
            <div className='bg-bigbackground '>
                <Row1 />
            </div>
            <div>
                <Row2 />
            </div>
            <div className='bg-secondbackground relative '>
                <div className='relative mx-auto container row3'>
                    <Row3 />
                </div>
            </div>
            <div className='row4 relative'>
                <img src={icone1} className='absolute left-0 bottom-0' alt="" />
                <img src={icone1} className='absolute right-0 bottom-0' alt="" />
                <Row4 />
            </div>
            <div className='bg-footer'>
                <Footer />
            </div>
        </div>
    );
};

export default Home;