import React from 'react';
import Button from '../button/button.component';
import Paragraph from '../paragraph/paragraph';
import Title from '../title/title.component';
import icone1 from '../../images/Emoticon/icons8-pencil-24.png';
import icone2 from '../../images/Emoticon/icons8-phone-case-24.png';
import { Link } from 'react-router-dom';
const Row4 = () => {
    return (
        <div className='container mt-20 mb-20 mx-auto space-y-8 pr-4 pl-4'>
            <Title color='text-black' textalign='text-center' fonsize='text-3xl mt-10 md:text-4xl md:mt-0' title='دیجی ‌اکسپرس' />
            <Paragraph fonsize='text-2xl' textalign='text-center' color='text-black' title='برای اینکه عضو دیجی اکسپرس بشی و کسب درآمد کنی کافیه اینجا کلیک کنی!' />
            <div className='flex flex-col justify-center md:flex-row  gap-8'>
           <Link to='/signup'><Button icone={icone1} className='bg-bigbackground text-title w-60 h-10' title='پیش‌ نیاز ها و ثبت نام' /></Link> 
            <Button icone={icone2} className='bg-white w-60  text-title h-10 border border-black' title='دانلود اپلیکیشن' />
            </div>
        </div>
    );
};

export default Row4;