import React from 'react';
import icone1 from '../../images/Emoticon/vehicles.1d4b4986.png';
import Button from '../button/button.component';
import Img from '../img/img.component';
import Input from '../input/input.component';
import Title from '../title/title.component';

const Row2 = () => {
    const data =[
        {title:'نام'},
        {title:'نام خانوادگی'},
        {title:'موبایل'},
        {title:'کد معرف'},
    ]
    return (
        <div className='container mx-auto pr-4 pt-20 pb-20 pl-4'>
            <div className='flex space-y-10 flex-col gap-24 items-center md:flex-row justify-between'>
                <div className='w-full'>
                    <Title textalign='text-center' fonsize='text-4xl mt-10 md:mt-0' color='text-white' title='پیش ثبت‌ نام' />
                   {data.map(item=>{
                       return (
                           <Input title={item.title}/>
                       )
                   })}
                   <Button className='w-full pt-3 mt-5 pb-3 bg-buttonmenu' title='تایید'/>
                </div>
                <div className='flex items-center justify-center'>
                    <div>
                    <Img src={icone1} />
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Row2;