import React from 'react';
import ImgBoxs from '../img-boxs/img-boxs';
import Title from '../title/title.component';

const Row2 = () => {
    const data = [
        {title:'۱. زمان و مکان',paragraph:'زمان و محل مطلوبت رو به ما بگو‍',src:'',bgImg:'bg-red-200'},
        {title:'۲. پیشنهادات',paragraph:'پیشنهادات ارسال بسته رو قبول کن',src:'',bgImg:'bg-yellow-200'},
        {title:'۳. ارسال بسته‌ها',paragraph:'بسته‌ها رو به مشتری‌ها برسون',src:'',bgImg:'bg-blue-200'},
        {title:'۴. دریافت پول',paragraph:'پول رو تو حسابت دریافت میکنی',src:'',bgImg:'bg-green-200'},
    ]
    return (
        <div className='container mt-20 mx-auto space-y-16 pr-4 pl-4'>
            <Title textalign='text-center' color='text-black' fonsize='text-3xl mt-10 md:text-4xl md:mt-0' title='توضیحات روند دیجی‌اکسپرس' />
           <div className='flex gap-16 flex-wrap justify-center'>
               <ImgBoxs model={data}/>
           </div>
        </div>
    );
};

export default Row2;